import React, { Component, Fragment } from "react";
import Header from "../Header";
import Footer from "../Footer";
import Projects from "../Projects";
import About from "../About";
import Skills from "../Skills";
import Thoughts from "../Thoughts";
import Quote from "../Quote";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: null
    };
  }

  componentDidMount() {
    document.title = `El Mehdi ASSALI - Portfolio`;
  }

  changeHeaderActive = active => {
    this.setState({ active });
  };

  render() {
    return (
      <Fragment>
        <Header active={this.state.active} />
        <main id="main">
          <div className="container-fluid">
            <Quote after changeHeaderActive={this.changeHeaderActive} />
            <About
              gray
              after
              before
              changeHeaderActive={this.changeHeaderActive}
            />
            <Skills after before changeHeaderActive={this.changeHeaderActive} />
            <Projects
              gray
              after
              before
              changeHeaderActive={this.changeHeaderActive}
            />
            <Thoughts before changeHeaderActive={this.changeHeaderActive} />
          </div>
        </main>
        <Footer />
      </Fragment>
    );
  }
}

export default Home;
